<style lang="scss" >
.video-new {
  position: relative;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  padding-top: 46px;
  background: var(--theme-background-black);
  // tab 
  .tab {
    background: rgba(243, 237, 237, 0.1);
    .van-nav-bar__title {
      color: #fff;
    }
  }

  // 
  .give {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 10px;
    right: 0px;
    top: 350px;
    width: 50px;
    height: 150px;
    background: rgba(242, 240, 240, 0.1);
    z-index: 99999;
  }
  .star {
    .container {
      /* , */
      cursor: pointer;
      /* ,var */
      --c: #ff6b81;
    }
    svg {
      width: 30px;
      /*  */
      overflow: visible;
      /*  */
      position: relative;
      z-index: 10;
    }
    #heart {
      fill: #eee;
      /* stroke,,, */
      stroke: var(--c);
      /*  */
      stroke-width: 40px;
      /* , */
      stroke-dasharray: 2600;
      /*  */
      stroke-dashoffset: 2600;
      /*  */
      stroke-linecap: round;
    }
    span {
      display: block;
      width: 12px;
      height: 12px;
      background-color: transparent;
      border-radius: 50%;
      /*   */
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      /*  */
      box-shadow: 0 -24px 0 var(--c), 0 24px 0 var(--c), -24px 0 0 var(--c),
        24px 0 0 var(--c), -18px -18px 0 var(--c), 18px -18px 0 var(--c),
        18px 18px 0 var(--c), -18px 18px 0 var(--c);
    }
    #checkbox:checked + svg #heart {
      /*     */
      animation: drawHeart 1s linear forwards;
    }
    #checkbox:checked ~ span {
      /*     */
      animation: blink 0.5s ease-in-out forwards;
      /*  */
      animation-delay: 0.85s;
    }
    #checkbox:checked + svg {
      /*     */
      animation: beat 1s linear forwards;
    }

    /*  */
    /*  */
    @keyframes drawHeart {
      0% {
        stroke: var(--c);
        stroke-dashoffset: 2600;
      }
      80% {
        stroke: var(--c);
        stroke-dashoffset: 0;
        fill: #eee;
      }
      100% {
        stroke: var(--c);
        stroke-dashoffset: 0;
        fill: var(--c);
      }
    }
    /*  */
    // @keyframes blink {
    //     0%{
    //         transform: translate(-50%,-50%) scale(0.5);
    //         opacity: 0.8;
    //     }
    //     50%{
    //         transform: translate(-50%,-50%) scale(1);
    //         opacity: 1;
    //     }
    //     100%{
    //         transform: translate(-50%,-50%) scale(1.1);
    //         opacity: 0;
    //     }
    // }
    /*  */
    @keyframes beat {
      0% {
        transform: scale(1);
      }
      70% {
        transform: scale(1);
      }
      80% {
        transform: scale(1.5);
      }
      100% {
        transform: scale(1);
      }
    }
  }
  .commen {
  }
}
</style>